<template>
  <div id="app">
    <div id="menu">
      <router-link @click="toggleMenu" style="color: #9ab7df" to="/">{{name}}</router-link>
      <router-link id="hhhidden" @click="toggleMenu" v-show="isHidden" to="/products"v-for="info in menuInfo">{{info}}</router-link>
      <a id="search-btn"
         to="/products"
         @click="toggleSearch"><i class="icon-search"></i></a>
      <transition name="fade">
        <input @keyup.enter="search"
               v-show="displaySearch" 
               type="text"
               placeholder="搜索莲藕加工产品">
      </transition>
    </div>
    <transition name="router" mode="out-in">
      <router-view></router-view>
    </transition>
    <div id="footer">
      <ul>
        <li><i class="icon-phone"></i><p>07163080208</p><p>13407131974</p></li>
        <li><i class="icon-envelop"></i><p>280548732@qq.com</p></li>
        <li><i class="icon-office"></i><p>监利县龚场镇白马村城东工业园</p></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      name: '益农水产品科技开发有限公司 ',
      displaySearch: false,
      menuInfo: [
        '莲藕',
        '藕带',
        '芦苇笋'
      ],
      isHidden: true
    }
  },
  methods: {
    toggleSearch () {
      this.displaySearch = !this.displaySearch
    },
    search () {
      console.log('searching...')
    },
    toggleMenu () {
      this.isHidden = !this.isHidden
    }
  },
  mounted () {
    this.$http({
      url: 'http://test.yowfung.cn/server/admin.php',
      method: 'post',
      data: {
        action: 'login',
        username: 'root',
        password: 'root'
      },
      withCredentials: true
    })
    .then(res => {
      console.log(res)
    })
    .catch(e => {
      console.log(e)
    })
  }
}
</script>

<style lang="stylus">
  .fade-enter-active
    transition all .3s ease
    
  .fade-leave-active
    transition all .3s ease
    
  .fade-leave, .fade-enter-to
    opacity 1
  
  .fade-leave-to, .fade-enter
    opacity 0

  .router-enter-active
    transition all .3s ease
    
  .router-leave-active
    transition all .3s ease
    
  .router-enter, .router-leave-to
    transform translateX(10px)
    opacity: 0

  body
    margin 0
    
  ul 
    margin 0
    padding 0
    list-style none
    
  a
    text-decoration none
    &:active,&:visited
      color #000

  #app
    box-sizing border-box
    font-family 'Avenir', Helvetica, Arial, sans-serif
    -webkit-font-smoothing antialiased
    -moz-osx-font-smoothing grayscale
    
  #menu
    margin 40px 0 0
    padding 25px 220px
    border-top 1px solid #7cb6e3
    font-size 0
    .visi
      visibility hidden
    a
      padding 25px 40px
      font-size 22px
      color #959595
      vertical-align top
      &:hover
        color #9ab7df
    #search-btn
      visibility hidden
      cursor pointer
      padding-right 16px
    input
      vertical-align top
      width 200px
      color #959595
      padding 0 0 0 8px
      font-size 20px
      outline none
      border none
      border-left 1px solid #959595
  
  #footer
    box-sizing border-box
    padding 36px 220px
    height 200px
    background #eeeeee
    font-size 0
    li
      padding 25px 0 0
      width 33%
      text-align center
      font-size 22px
      color #666666
      display inline-block
      vertical-align top
      p
        margin 10px 0
</style>



