<template>
  <div id="index">
    <div id="carousel">
      <ul id="img-list">
        <transition-group name="fade" mode="out-in">
          <li v-for="(src, index) in imgList"
              v-show="activeIndex === index"
              :key="src">
            <img :src="src" alt="photo">
          </li>
        </transition-group>
      </ul>
      <ul id="btn-list">
        <li v-for="(src, index) in imgList"
            :class="{active: activeIndex === index}"
            @click="tabImg(index)"></li>
      </ul>
    </div>
    <div id="introd">{{introd}}</div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      introd: '监利县益农水产品科技开发有限公司，由监利县政府招商引资于2014年11月26日注册落户龚场镇，注册资本达三千万元，法定代表人为曾建平。经营范围：水产品养殖、收购、加工、销售；水生蔬菜、藕带种植、收购、研发、生产、销售；稻谷收购、加工、销售。公司为县级重点龙头企业，2015年藕带加工生产线已建成投产，今年计划生产泡淑藕带和莲藕产品200万包。',
      activeIndex: 0,
      imgList: [
        '../../static/imgs/p1.jpg',
        '../../static/imgs/p2.jpg',
        '../../static/imgs/p3.jpg'
      ]
    }
  },
  methods: {
    tabImg (index) {
      this.activeIndex = index
    }
  },
  mounted () {
    this.timer = setInterval(() => {
      this.activeIndex++
      if (this.activeIndex === 3) this.activeIndex = 0
    }, 3000)
  }
}
</script>

<style lang="stylus" scoped>
  .fade-enter-active
    transition all 1s ease
    
  .fade-leave-active
    transition all 1s ease
    
  .fade-leave, .fade-enter-to
    opacity 1
  
  .fade-leave-to, .fade-enter
    opacity 0

  #carousel
    height 500px
    position relative
    img
      position absolute
      width 100%
      height 500px
    #btn-list
      right 70px
      top 180px
      position absolute
      li
        width 10px
        height 10px
        z-index 100
        border-radius 50%
        margin 10px
        border 2px solid #666
        cursor pointer
      .active
        border-color #ff5252
  #introd
    margin 40px 220px 80px 220px
    line-height 30px
    color #7f7f7f
    word-break break-all
</style>



