import Vue from 'vue'
import Router from 'vue-router'

import index from '@/components/index'
import products from '@/components/products'
import detail from '@/components/detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/products',
      name: 'products',
      component: products,
      children: [
        {
          path: 'a',
          component: detail
        }
      ]
    }
  ]
})



